@import url(https://fonts.googleapis.com/css?family=EB+Garamond:400,400i);
.main {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1em;
  height: 100%; }
  @media screen and (min-width: 760px) {
    .main {
      width: 50%; } }

.content {
  width: 88.8%;
  height: 77.7%;
  text-align: justify; }
  @media screen and (min-width: 760px) {
    .content {
      width: 55.5%;
      height: 66.6%; } }

.header {
  position: absolute;
  top: 4em;
  right: 0; }

.name {
  margin: 0;
  padding: 0;
  font-size: 1.15rem;
  font-style: italic;
  font-weight: 400;
  -webkit-transform: rotate3d(0, 0, 1, -90deg);
          transform: rotate3d(0, 0, 1, -90deg); }

.lazyload,
.lazyloading {
  opacity: 0; }

.lazyloaded {
  opacity: 1;
  -webkit-transition: opacity 300ms;
  transition: opacity 300ms; }

.gallery {
  display: none; }
  @media screen and (min-width: 768px) {
    .gallery {
      position: relative;
      display: block;
      width: 50%; } }

.gallery-figure {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: 0; }
  .gallery-figure > img {
    height: 100%;
    width: 100%;
    display: block;
    object-fit: cover; }
    .is-inverted .gallery-figure > img {
      -webkit-filter: invert(0.8);
              filter: invert(0.8); }

.footer {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  padding: 2em; }

html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: 'EB Garamond', serif;
  font-size: 1em;
  line-height: 1.4;
  letter-spacing: .025em; }

a {
  position: relative;
  display: inline-block;
  color: inherit;
  font-size: 1.15rem;
  text-decoration: none;
  border-bottom: solid 1px transparent;
  -webkit-transition: border-color .5s ease-in-out, color .5s ease-in-out, background-color .5s ease-in-out, padding .25s ease-in;
  transition: border-color .5s ease-in-out, color .5s ease-in-out, background-color .5s ease-in-out, padding .25s ease-in; }
  a.selected {
    padding-right: .5em;
    padding-left: .5em;
    color: #fefefe;
    background-color: #222; }
    .is-inverted a.selected {
      color: #222;
      background: #fefefe; }
  a:hover {
    border-color: inherit; }

.wrapper {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  color: #222;
  background-color: #fefefe;
  -webkit-transition: color .5s ease-in-out, background-color .5s ease-in-out;
  transition: color .5s ease-in-out, background-color .5s ease-in-out; }
  @media screen and (min-width: 768px) {
    .wrapper {
      display: flex; } }
  .wrapper.is-inverted {
    color: #fefefe;
    background: #222; }

